.patient-view {
  height: auto;
  display: inline-block;
  padding: 30px;
  margin: 0 0 20px;
  vertical-align: top;
  width: 100%; }

.half-view {
  width: 50%; }

.view-title {
  padding: 0 0 10px;
  margin: 0 0 10px;
  font-size: 1.5em;
  letter-spacing: -0.025em;
  color: #384E77;
  /* $color-primary */
  border-bottom: 2px solid #eee; }

.patient-data-text {
  font-size: 1em;
  margin: 0 0 20px;
  color: #888; }

@media (max-width: 975px) {
  .patient-view {
    padding: 10px;
    display: inline-block;
    position: relative; }
  .half-view {
    width: 100%;
    display: inline-block; } }



