.container {
  min-height: 100vh;
  width: 100%;
  display: -webkit-box;
  display: -webkit-flex;
  display: -ms-flexbox;
  display: flex;
  overflow: hidden;
  position: relative; }

.pin {
  position: fixed;
  top: 0;
  width: 100%;
  height: inherit;
  z-index: 100; }

.pin + .container {
  padding-top: 36px; }

@media (max-width: 975px) {
  .container {
    display: inline-block;
    display: block;
    height: auto; } }



